import { Component, OnInit, HostListener } from '@angular/core';

import { ApartmentService } from '../../../../../api/services/apartment.service';
import { AccountsService } from '../../../../.././api/services/accounts.service';
import { SharedService } from '../../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-gl-other-reprots-transactions',
  templateUrl: './gl-other-reprots-transactions.component.html',
  styleUrls: ['./gl-other-reprots-transactions.component.scss']
})
export class GlOtherReprotsTransactionsComponent implements OnInit {

  reports:any;

  glTypes:any = [];
  glGroups:any = [];
  glAccounts:any = [];

  glTypeSelectedIndex:any = null;
  glGroupSelectedIndex:any = null;
  glAccountSelectedIndex:any = null;

  isGlGroupsLoaded:boolean = true;
  isGlAccountsLoaded:boolean = true;

  isDataLoaded:boolean = true;

  reportsTransactionsData:string = "";
  reportsTransactionsDataList:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 8;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  totalDebitAmount:number = 0;
  totalCreditAmount:number = 0;

  isMobile:boolean = false;

  isDataSubmitted:boolean = false;

  constructor(
  	private apartmentService:ApartmentService,
  	private accountsService:AccountsService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { 

  }

  isGroupsEmpty(){
  	return this.glGroups.length == 0 && this.isGlGroupsLoaded ? true : false;
  }

  isGroupAccountsEmpty(){
  	return this.glAccounts.length == 0 && this.isGlAccountsLoaded ? true : false;
  }

  setGlTypeIndex(id){
  	return this.glTypeSelectedIndex == id ? 'active' : ''
  }

  setGlGroupIndex(id){
  	return this.glGroupSelectedIndex == id ? 'active' : ''
  }

  setGlAccountIndex(id){
  	return this.glAccountSelectedIndex == id ? 'active' : ''
  }

  setGlAccountName(id){
  	this.glAccountSelectedIndex = id;
  }

  getGlGroups(id){

  	this.isGlGroupsLoaded = false;

  	var params = {
  		GlaccountTypeId: id,
  		apartmentId: parseInt(this.cookieService.get('apartmentId'))
  	}

  	this.accountsService.getAllGLGroupsByAccountTypeID(params).subscribe((res:any) => {
  		this.isGlGroupsLoaded = true;
  		this.glGroups = res;
  		this.glTypeSelectedIndex = id;
  	});

  }

  getGlAccount(id){

  	this.isGlAccountsLoaded = false;

  	var params = {
  		apartmentId: parseInt(this.cookieService.get('apartmentId')),
  		groupId: id
  	}

  	this.accountsService.getGLAccountsByGroupId(params).subscribe((res:any) => {
  		this.isGlAccountsLoaded = true;
  		this.glAccounts = res;
  		this.glGroupSelectedIndex = id;
  	})

  }

  isGroupsSelected(){
  	return (this.glTypeSelectedIndex != null) && (this.glGroupSelectedIndex != null) && (this.glAccountSelectedIndex != null) ? true : false;
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  getDate(date){
    return moment(date).format("MM-DD-YYYY");
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable(){
    return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }

  submitGlreportsTransactionForm(form){

    this.isDataSubmitted = true;

    this.isDataLoaded = false;

    var params = {
      FromDate: moment(this.reports.fromDate).format("YYYY-MM-DD"),
      Todate: moment(this.reports.toDate).format("YYYY-MM-DD"),
      ApartmentId: parseInt(this.cookieService.get('apartmentId')),
      //GlAccountTypeID: this.glTypeSelectedIndex,
      //GLGroupID: this.glGroupSelectedIndex,
      GLAccountId: this.glAccountSelectedIndex
    }

    this.accountsService.GetGLAccountTransactionsByID(params).subscribe((res:any) => {

      this.totalDebitAmount = 0;
      this.totalCreditAmount = 0;

      this.isDataLoaded = true;

      this.reportsTransactionsDataList = res;

      this.totalItems = this.reportsTransactionsDataList.length;

      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      _.each(this.reportsTransactionsDataList, (item, index) => {

        this.totalDebitAmount = this.totalDebitAmount + item.debit;
        this.totalCreditAmount = this.totalCreditAmount + item.credit;

      });

      this.glTypeSelectedIndex = null;
      this.glGroupSelectedIndex = null;
      this.glAccountSelectedIndex = null; 


    })

  }


  ngOnInit() {

  	this.reports = {};

  	this.glTypes = [
  		{name: 'Assets', id: 158},
  		{name: 'Liabilities', id: 164},
      {name: 'Equity-Member Fund', id: 280},
  		{name: 'Income', id: 165},
  		{name: 'Expenses', id: 166}
  	];

    this.reportsTransactionsDataList = [];
    this.totalItems = this.reportsTransactionsDataList.length;

  }

}
