import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { GeneralLedgerComponent } from './general-ledger.component';
import { GlAssetsComponent } from './components/gl-assets/gl-assets.component';
import { GlLiablilitiesComponent } from './components/gl-liablilities/gl-liablilities.component';
import { GlIncomeComponent } from './components/gl-income/gl-income.component';
import { GlExpenseComponent } from './components/gl-expense/gl-expense.component';
import { GlAssetGroupsComponent } from './components/gl-groups-wrapper/gl-asset-groups/gl-asset-groups.component';
import { AddGlGroupComponent } from './components/add-gl-group/add-gl-group.component';
import { GlGroupsWrapperComponent } from './components/gl-groups-wrapper/gl-groups-wrapper.component';
import { GlLiabilitesGroupsComponent } from './components/gl-groups-wrapper/gl-liabilites-groups/gl-liabilites-groups.component';
import { GlIncomeGroupsComponent } from './components/gl-groups-wrapper/gl-income-groups/gl-income-groups.component';
import { GlExpenseGroupsComponent } from './components/gl-groups-wrapper/gl-expense-groups/gl-expense-groups.component';
import { AddGlAccountComponent } from './components/add-gl-account/add-gl-account.component';
import { GlReportsComponent } from './components/gl-reports/gl-reports.component';
import { GlSettingsComponent } from './components/gl-settings/gl-settings.component';
import { GlReportTypeComponent } from './components/gl-reports/gl-report-type/gl-report-type.component';
import { GlReportTransactionsComponent } from './components/gl-reports/gl-report-transactions/gl-report-transactions.component';
import { GlJournalsComponent } from './components//gl-journals/gl-journals.component';
import { GlCreateJournalComponent } from './components/gl-journals/gl-create-journal/gl-create-journal.component';
import { GlAllJournalsComponent } from './components/gl-journals/gl-all-journals/gl-all-journals.component';
import { GlCreateJournalFieldsComponent } from './components/gl-journals/gl-create-journal-fields/gl-create-journal-fields.component';
import { GlJournalsBulkComponent } from './components/gl-journals/gl-journals-bulk/gl-journals-bulk.component';
import { GlJournalsBulkCorrectionsComponent } from './components/gl-journals/gl-journals-bulk-corrections/gl-journals-bulk-corrections.component';
import { GlOtherReportsComponent } from './components/gl-other-reports/gl-other-reports.component';
import { GlOtherReprotsTransactionsComponent } from './components/gl-other-reports/gl-other-reprots-transactions/gl-other-reprots-transactions.component';
import { GlFinancialStatementsComponent } from './components/gl-other-reports/gl-financial-statements/gl-financial-statements.component';
import { GlEquityMemberFundGroupsComponent } from './components/gl-groups-wrapper/gl-equity-member-fund-groups/gl-equity-member-fund-groups.component';
import { GlEquityMemberFundComponent } from './components/gl-equity-member-fund/gl-equity-member-fund.component';

const routes: Routes = [
	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'settings', component: GlSettingsComponent,
		children: [
			{ path: '', redirectTo: 'assets', pathMatch: 'full'},
			{ path: 'groups', component: GlGroupsWrapperComponent},
			{ path: 'liabilities', component: GlLiablilitiesComponent},
			{ path: 'equity', component: GlEquityMemberFundComponent},
			{ path: 'income', component: GlIncomeComponent},
			{ path: 'expense', component: GlExpenseComponent},
			{ path: 'assets', component: GlAssetsComponent},
			{ path: 'add-group', component: AddGlGroupComponent},
			{ path: '**', redirectTo: 'assets', pathMatch: 'full'}
		]
	},
	{ path: 'journals', component: GlJournalsComponent, canActivate: [AuthGuard],
		children: [
			{ path: '', redirectTo: 'add-journal', pathMatch: 'full'},
			{ path: 'add-journal', component: GlCreateJournalComponent},
			{ path: 'edit-journal/:id', component: GlCreateJournalComponent},
			{ path: 'all-journals', component: GlAllJournalsComponent},
			{ path: 'bulk-posting', component: GlJournalsBulkComponent},
			{ path: 'bulk-corrections', component: GlJournalsBulkCorrectionsComponent},
			{ path: '**', redirectTo: 'add-journal', pathMatch: 'full'}
		]
	},
	{ path: 'trial-balances', component: GlReportsComponent },
	{ path: 'transactions/:type/:fromdate/:todate/:id', component: GlReportTransactionsComponent },
	{ path: 'all-reports', component: GlOtherReportsComponent },
	{ path: 'report/transactions/:subtype/:id', component: GlOtherReprotsTransactionsComponent },
	{ path: 'report/statements/:subtype/:id', component: GlFinancialStatementsComponent },
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class GeneralLedgerRoutingModule { }
