import { Component, OnInit, Input } from '@angular/core';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-gl-report-type',
  templateUrl: './gl-report-type.component.html',
  styleUrls: ['./gl-report-type.component.scss']
})
export class GlReportTypeComponent implements OnInit {
  
  totalDebitAmount:number = 0;
  totalCreditAmount:number = 0;

  totalItems:any;
  @Input() glReportsDataList:any;
  @Input() group:any;
  @Input() fromDate:any;
  @Input() toDate:any;

  constructor() { }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable(){
  	return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
  	return this.totalItems == 0 ? true : false;
  }

  ngOnInit() {

    console.log(this.glReportsDataList);

  	this.glReportsDataList = this.glReportsDataList.filter(item => {
  		return item.description.indexOf(this.group) != -1;
  	})

    

  	this.totalItems = this.glReportsDataList.length;

	_.each(this.glReportsDataList, (item, index) => {

		this.totalDebitAmount = this.totalDebitAmount + item.debit;
		this.totalCreditAmount = this.totalCreditAmount + item.credit;

	});

  }

}
