import { Component, OnInit, Input, Injector } from '@angular/core';

import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { AccountsService } from '../../../.././api/services/accounts.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import { ModalService } from '../../../../shared/services/modal.service';

@Component({
  selector: 'app-add-gl-group',
  templateUrl: './add-gl-group.component.html',
  styleUrls: ['./add-gl-group.component.scss']
})
export class AddGlGroupComponent implements OnInit {
 

  gl:any;

  isGroupAdded:boolean = true;

  @Input() type:any;
  @Input() accountTypeId:any;
  @Input() group:any;
  @Input() glgroupId?:any;

  modalService:any;

  constructor(
    private injector: Injector,
    public dialog: MatDialog,
    private router: Router,
    private route: ActivatedRoute,
    private accountsService:AccountsService,
  	private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) {
    this.modalService = this.injector.get(ModalService);
   }

  isCreateGroup(){
    return this.type == "add";
  }

  submitGLGroupForm(form){

    this.isGroupAdded = false;

    if(this.isCreateGroup()){

      let details = {

        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "glnumber": parseInt(this.gl.glnumber),
        "name": this.gl.name,
        "glaccountTypeId": this.accountTypeId, //assets
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": 0,
        "updatedOn": new Date().toISOString()

      }

      this.accountsService.addGLGroup(details).subscribe((res:any) => {

        this.isGroupAdded = true;

        if(res.message) {
          this.sharedService.setAlertMessage("Group added successfully");
          this.sharedService.setGlGroupAdded({id:this.accountTypeId, status: true});
        }
        else {
          this.modalService.showErrorModal(res.errorMessage);
          this.sharedService.setGlGroupAdded({id:this.accountTypeId, status: false});
        }


      })

    }
    else {

      let details = {

        "glgroupId": parseInt(this.glgroupId),
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "glnumber": parseInt(this.gl.glnumber),
        "name": this.gl.name,
        "glaccountTypeId": this.accountTypeId, //assets
        "isActive": true,
        "insertedBy": 0,
        "insertedOn": new Date().toISOString(),
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString()

      }

      this.accountsService.updateGLGroup(details).subscribe((res:any) => {

        this.isGroupAdded = true;

        if(res.message) {
           this.sharedService.setAlertMessage("Group updated successfully");
           this.sharedService.setGlGroupAdded({id:this.accountTypeId, status: true});
        }
        else {
            this.modalService.showErrorModal(res.errorMessage);
            this.sharedService.setGlGroupAdded({id:this.accountTypeId, status: false});
        }


      })

    }
  	

  }

  ngOnInit() {

  	this.gl = {};
    this.sharedService.setGlGroupAdded({id:this.accountTypeId, status: false});

    if(!this.isCreateGroup()){

       var params = {
          apartmentId: parseInt(this.cookieService.get('apartmentId')),
          glGroupId: this.glgroupId,
       }

      this.accountsService.getGLGroupById(params).subscribe((res:any) => {

        this.gl = res[0];

      })
    }
   
  }

}
