import { Component, OnInit, Injector, Input, Output, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { ControlContainer, NgForm, FormsModule } from '@angular/forms';

import { JournalService } from '../../../../.././api/services/journal.service';
import { SharedService } from '../../../../../shared/services/shared.service';
import { ModalService } from '../../../../../shared/services/modal.service';
import { LookupService } from '../../../../../api/services/lookup.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';


@Component({
  selector: 'app-gl-all-journals',
  templateUrl: './gl-all-journals.component.html',
  styleUrls: ['./gl-all-journals.component.scss']
})
export class GlAllJournalsComponent implements OnInit {

  journalsDataList:any;
  journalData:string = "";
  
  isDataLoaded:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "documentNumber";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  modalService:any;

  fromDate:any;
  toDate:any;

  constructor(
    private injector: Injector,
    public dialog: MatDialog,
  	private router: Router,
    private route: ActivatedRoute,
    private journalService:JournalService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  getDate(date){
  	return moment(date).format("MM-DD-YYYY");
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable(){
  	return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
  	return this.totalItems == 0 ? true : false;
  }

  getUser(id){
  	return id;
  }

  getDateFormat(date){
  	return moment(date).format("MM-DD-YYYY");
  }

  showConfirmModal(index) {
    this.modalService.showConfirmModal(index);
  }

  getJournals(){

    let details = {
      ApartmentId: parseInt(this.cookieService.get('apartmentId')),
      FromDate: this.fromDate,
      Todate:  this.toDate
    }

    this.journalService.getAllJournalsByApartmentId(details).subscribe((res:any) => {

      this.journalsDataList = res.filter(item => {
        return item.active
      });

      this.totalItems = this.journalsDataList.length;

      if(this.totalItems>this.itemLimit){
      this.ItemEndIndex = this.itemLimit;
    }
    else {
      this.ItemEndIndex = this.totalItems;
    }

    this.isDataLoaded = true;

    }, error => {
      console.log(error);
    });

  }


  ngOnInit() {

  	// fetch data for past one week by default
  	this.fromDate = moment().subtract(1, 'week').format("YYYY-MM-DD");
  	this.toDate = moment().add(1, 'days').format("YYYY-MM-DD");

  	this.getJournals();

    // delete item
    this.sharedService.unitlistdeleteindexcast.subscribe(id => {

      if(id != null){

        var params = {
          JournalId: id,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }

        this.isDataLoaded = false;

        this.journalService.deleteJournal(params).subscribe((res:any) => {

          this.sharedService.setAlertMessage("Journal deleted");
          this.getJournals();
          this.sharedService.setUnitListDeleteIndex(null);

        }, error => {
          console.log(error);
        });

        
      }

    });



  }

}
