import { Component, OnInit, Injector, HostListener, Input } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { ApartmentService } from '../../../../../api/services/apartment.service';
import { AccountsService } from '../../../../.././api/services/accounts.service';
import { SharedService } from '../../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import { ModalService } from '../../../../../shared/services/modal.service';

import * as _ from 'underscore';

@Component({
  selector: 'app-gl-asset-groups',
  templateUrl: './gl-asset-groups.component.html',
  styleUrls: ['./gl-asset-groups.component.scss']
})
export class GlAssetGroupsComponent implements OnInit {

  glGroupsDataList:any;
  glGroupsData:string = "";

  isDataLoaded:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 8;
  totalFilterItems:any;

  unitFieldType:string = "glnumber";
  unitOrder:boolean = true;

  modalService:any;

  @Input() glaccountTypeId;

  accountDeleteTypeId:any;

  constructor(
  	private injector: Injector,
    public dialog: MatDialog,
  	private apartmentService:ApartmentService,
  	private accountsService:AccountsService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { 
  	this.modalService = this.injector.get(ModalService);
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  showConfirmModal(id) {
    this.sharedService.setGlGroupDeleteTypeId(parseInt(this.glaccountTypeId));
    this.modalService.showConfirmModal(id);
  }

  isItemsAvailable(){
  	return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
  	return this.totalItems == 0 ? true : false;
  }

  stopPropagation(event: Event){
    event.stopPropagation();
  }

  getGlGroups(){

    this.isDataLoaded = false;

    this.accountsService.getAllGLGroups().subscribe((res:any) => {
      
      this.glGroupsDataList = res.filter(item => {
        return item.isActive && (item.glaccountTypeId == parseInt(this.glaccountTypeId)) // active and type id
      });

      this.isDataLoaded = true;

      this.totalItems = this.glGroupsDataList.length;

      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      //sorting
      this.sortUnitData('glnumber');

    });

  }

  ngOnInit() {

  	
    this.getGlGroups();

    this.sharedService.glgroupdeletetypeidcast.subscribe(typeId => this.accountDeleteTypeId = typeId);

    // delete item
    this.sharedService.unitlistdeleteindexcast.subscribe(id => {

      if(id != null && (this.accountDeleteTypeId == parseInt(this.glaccountTypeId))  ){

        var params = {
          apartmentId: parseInt(this.cookieService.get('apartmentId')),
          glGroupId: id,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }

        this.accountsService.deleteGLGroup(params).subscribe((res:any) => {

          if(res.message){

            _.each(this.glGroupsDataList, (type)=>{
              if(type.glgroupId == id){
                type.isActive = false;
              }
            })

            setTimeout(() => {

              this.glGroupsDataList = this.glGroupsDataList.filter((type) => type.glaccountId !== id);
              this.totalItems = this.glGroupsDataList.length;
              this.sharedService.setAlertMessage("Gl Group deleted");
              this.sharedService.setUnitListDeleteIndex(null);

            }, 500)

          }
          else {
            this.modalService.showErrorModal(res.errorMessage);
            this.sharedService.setUnitListDeleteIndex(null);
          }


        }, error => {
          console.log(error);
        });

        
      }

    });


    //update tabular data
    this.sharedService.glgroupaddedcast.subscribe(data => {

      if(data.status && data.id == parseInt(this.glaccountTypeId)){
        this.getGlGroups();
      }

    })

  }

}
